@import url('https://fonts.googleapis.com/css2?family=Langar&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Indie+Flower&family=Sniglet&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');


  .bodyLogin {
    background: url(../images/login.png)
    no-repeat;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  main h1 {
    text-align: center;
    margin-top: 10%;
    text-shadow: 0px 0px 25px white;
    font-size: 3em;
    color: black;
    font-family: 'Dancing Script', cursive;
  }

  main form {
    margin-top: 4%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15%;
  }

  main form input {
    width: 35%;
    text-align: center;
    background: rgba(39, 35, 35, 0.3);
    color: #6b6470;
    font-style: unset;
    font-size: 2em;
    font-weight: 100;
    letter-spacing: 2px;
    box-shadow:  0px 0px 10px white;
    border-radius: 9px;
    font-family: 'Sniglet', cursive;
  }

  main form button {
    margin-top: 40%;
    margin-left: -50%;
    width: 200%;
    font-size: 1.5em;
    font-weight: 100;
    letter-spacing: 2px;
    border-radius: 5px;
    background-color: #281646;
    color: #6b6470;
    box-shadow:  0px 0px 10px white;
    font-family: 'Sniglet', cursive;
  }

  .logoLogin {
    display: flex;
    justify-content: center;
  }
  
  .logo {
    margin-top: 15%;
    width: 40%;
  }

  .div-footer {
    text-align: center;
    color: #faeff1;
    bottom: 0;
    position: absolute;
    font-size: 0.6em;
    clear: both;
    font-family: 'Roboto', 'sans-serif';
  }

@media screen and (min-width: 812px) {
  .bodyLogin {
    background: url(../images/login.png)
    no-repeat;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background-size: cover;
  }

  main h1 {
    font-size: 5em;
    margin-top: 1%;
  }

  main form input {
    width: 100%;
    font-size: 2em;
  }

  main form button {
    margin-top: 40%;
    margin-left: -50%;
    width: 200%;
    font-size: 2em;
  }

  .logo {
    width: 100%;
  }

  .div-footer {
    font-size: 1em;
  }
}
