@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Indie+Flower&family=Sniglet&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Della+Respira&family=Indie+Flower&family=Langar&family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Della+Respira&family=Indie+Flower&family=Langar&family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bodyLogin {
    background: url(/tarot-vision/static/media/login.453aae10.png)
    no-repeat;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  main h1 {
    text-align: center;
    margin-top: 10%;
    text-shadow: 0px 0px 25px white;
    font-size: 3em;
    color: black;
    font-family: 'Dancing Script', cursive;
  }

  main form {
    margin-top: 4%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15%;
  }

  main form input {
    width: 35%;
    text-align: center;
    background: rgba(39, 35, 35, 0.3);
    color: #6b6470;
    font-style: unset;
    font-size: 2em;
    font-weight: 100;
    letter-spacing: 2px;
    box-shadow:  0px 0px 10px white;
    border-radius: 9px;
    font-family: 'Sniglet', cursive;
  }

  main form button {
    margin-top: 40%;
    margin-left: -50%;
    width: 200%;
    font-size: 1.5em;
    font-weight: 100;
    letter-spacing: 2px;
    border-radius: 5px;
    background-color: #281646;
    color: #6b6470;
    box-shadow:  0px 0px 10px white;
    font-family: 'Sniglet', cursive;
  }

  .logoLogin {
    display: flex;
    justify-content: center;
  }
  
  .logo {
    margin-top: 15%;
    width: 40%;
  }

  .div-footer {
    text-align: center;
    color: #faeff1;
    bottom: 0;
    position: absolute;
    font-size: 0.6em;
    clear: both;
    font-family: 'Roboto', 'sans-serif';
  }

@media screen and (min-width: 812px) {
  .bodyLogin {
    background: url(/tarot-vision/static/media/login.453aae10.png)
    no-repeat;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background-size: cover;
  }

  main h1 {
    font-size: 5em;
    margin-top: 1%;
  }

  main form input {
    width: 100%;
    font-size: 2em;
  }

  main form button {
    margin-top: 40%;
    margin-left: -50%;
    width: 200%;
    font-size: 2em;
  }

  .logo {
    width: 100%;
  }

  .div-footer {
    font-size: 1em;
  }
}

.bodyMain {
  background-image: url(/tarot-vision/static/media/purpleuniverse.4718e845.jpg);
  background-repeat: no-repeat;
  height: 100vh;
  background-size: cover;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.question {
  margin-top: 25%;
  text-align: center;
  color: white;
  font-size: 3em;
  font-family: 'Dancing Script', cursive;
  text-shadow: 0px 0px 25px white;
}

.div-answer {
  text-align: center;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 6%;
}

.button {
  margin-top: 10%;
  margin-bottom: 15%;
  width: 45%;
  font-size: 1.4em;
  font-weight: 100;
  line-height: 40px;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: #281646;
  box-shadow:  0px 0px 10px rgb(119, 119, 119);
  font-family: 'Sniglet', cursive;
}

.div-footer {
  margin-top: 10%;
  text-align: center;
  color: #faeff1;
  font-family: 'Roboto', 'sans-serif';
  bottom: 0;
  position: absolute;
}

@media screen and (min-width: 812px) {
  .question {
    font-size: 4.5em;
  }

  .button {
    font-size: 2em;
    line-height: 50px;
    width: 25%;
  }

  .div-footer {
    font-size: 1em;
  }
}

.bodyAnswer {
  background-image: url(/tarot-vision/static/media/purpleuniverse.4718e845.jpg);
  background-repeat: no-repeat;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.text-answer {
  color: white;
  font-family: 'Della Respira', serif;
  font-size: 1.4em;
  line-height: 37px;
}

.div-image {
  margin-top: 5%;
}

.h2answer {
  text-shadow: 0px 0px 25px white;
}

.div-answer {
  text-align: center;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 2%;
}

.play-again {
  margin-top: 5%;
  width: 45%;
  font-size: 1.4em;
  font-weight: 100;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: #281646;
  color: white;
  box-shadow:  0px 0px 10px rgb(119, 119, 119);
  font-family: 'Sniglet', cursive;
}

.go-home {
  margin-bottom: 10%;
  width: 100%;
  font-size: 1.4em;
  font-weight: 100;
  letter-spacing: 2px;
  border-radius: 5px;
  background-color: #281646;
  color: white;
  box-shadow:  0px 0px 10px rgb(119, 119, 119);
  font-family: 'Sniglet', cursive;
}

.div-footer-answer  {
  text-align: center;
  color: #faeff1;
  font-family: 'Roboto', 'sans-serif';
  bottom: 0;
  font-size: 0.6em;
  margin-top: 5%;
}

@media screen and (min-width: 812px) {
  .text-answer {
    font-size: 1.7em;
    line-height: 40px;
  }

  .play-again {
    margin-top: 5%;
    margin-bottom: 0;
    width: 25%;
    font-size: 2em;
  }

  .go-home {
    margin-bottom: 0;
    width: 100%;
    font-size: 2em;
  }
  .div-footer-answer  {
    font-size: 1em;
  }

  .h2answer {
    font-size: 3em;
  }
}

